header {
  position: relative;
  width: 100%;
  height: 50px;
  padding: 10px 0;
  display: grid;
  grid-template-columns: 40% 60%;
  font-size: 0.8rem;

  .title {
    position: absolute;
    left: 50%;
    top: 15px;
    transform: translateX(-50%);
    min-width: 225px;

    @media screen and (max-width : 768px) {
      top: 50px;
    }
  }
}