 .card {
   flex: 1 0 17%;
   position: relative;
   min-width: 190px;
   width: 170px;
   max-width: 280px;
   margin: 5px;
   height: 450px;
   background: $blue2;
   padding: 10px;
   box-shadow: -1px -1px 34px -13px rgba(255, 230, 3, 0.2);
   overflow: hidden;


   .card-container {
     img {
       width: 100%;
       height: 200px;
       border-radius: 10px;
       object-fit: center/cover;
     }

     h3 {
       font-size: 1rem;
       padding: 5px 0;
     }

     p {
       font-size: 0.8rem;
       color: rgb(202,
           194,
           186);
     }

     .genre {
       display: flex;
       flex-wrap: wrap;

       li {
         margin-right: 5px;
         background: $blue1;
         padding: 2px 5px;
         border-radius: 5px;
         display: flex;
         justify-content: center;
         align-items: center;
         font-size: 0.7rem;
       }
     }

     .overview {
       line-height: 20px;
       height: 100px;
       overflow-y: scroll;
       padding-bottom: 50px;

       &::-webkit-scrollbar {
         display: none;
       }
     }

     button {
       position: absolute;
       bottom: 0;
       left: 0;
       width: 100%;
       padding: 10px;
       cursor: pointer;
       background: rgb(250, 180, 4);
       border: none;
       font-size: 1rem;
       font-weight: 600;
       transition: 0.1s ease;

       &:active {
         background: $blue3;
       }

       &:hover {
         background: $blue3;
       }

       i {
         color: $blue2;
       }
     }
   }
 }