.home {
  padding: 0 10px;

  .feactures {
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 20px 0 10px;

    @media screen and (max-width: 768px) {
      margin-top: 30px;
    }

    .search-container {
      width: 200px;
      height: 70px;
      background: $blue3;
      border-radius: 10px;
      text-align: center;
      display: grid;
      grid-template-rows: 50% 50%;
      overflow: hidden;

      input {
        outline: none;
        color: black;
        text-align: center;

        &::placeholder {
          color: black;
          text-align: center;
        }
      }

      label {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 1rem;
        font-weight: 700;
      }
    }

    .sort-container {
      width: 100%;
      display: grid;
      grid-template-columns: 50% 50%;
      background: $blue3;
      margin: 10px 0;
      border-radius: 15px;
      overflow: hidden;

      input[type="radio"] {
        display: none;
      }

      input[type="radio"]:checked+label {
        transform: scale(1.1);
        color: rgb(255, 196, 0);
        font-weight: 800;

      }

      .top,
      .flop {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0 5px;

        &:active {
          background: $blue1;
        }

        &:hover {
          background: $blue1;
        }
      }
    }
  }

  .movie-display {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
}